.example {
  display: flex;
  flex-wrap: wrap;
  width: 100%;

  background-color: @grey;
}

.example__big-wrapper {
  display: flex;
  flex-wrap: wrap;
  margin: 0 auto;
}

.example__wrapper {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
}

.example__content {
  display: flex;
  flex-wrap: wrap;
  width: 320px;
  margin: 0 auto;
  padding: 28px 20px 34px;
}

.example__title {
  margin: 0;
  margin-bottom: 37px;

  .font-apply();
  font-size: 36px;
  line-height: 36px;
  color: @black;
}

.example__info {
  width: 266px;
  margin: 0;
  margin-bottom: 23px;

  .font-apply(@font-family: "Arial");
  font-size: 14px;
  line-height: 18px;
  color: @greyish-brown;
}

.example__statistic {
  width: 280px;
  margin-bottom: 17px;
}

.example__costs {
  margin: 0 auto;

  .font-apply(@text-align: center);
  font-size: 14px;
  line-height: 20px;
  color: @black;

  text-transform: uppercase;
}

.example__visual {
  width: 320px;
  position: relative;
  margin: 0 auto;
  padding: 271px 60px 38px;

  background-color: @grey;
}

.example__image {
  width: 320px;
  height: 240px;

  &--before,
  &--after {
    display: none;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 0;
  }
}

.example__toggles {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;

  &::after {
    content: "";

    position: absolute;
    top: 5px;
    left: 55px;
    z-index: 7;

    width: 84px;
    height: 14px;
    border-radius: 6px;

    background-color: @white;

    border: solid 1px @pinkish-grey;
  }
}

.example__toggle {
  position: relative;

  .font-apply();
  font-size: 16px;
  line-height: 20px;
  color: @black;

  text-transform: uppercase;

  &--left::before,
  &--right::before {
    display: none;

    content: "";

    position: absolute;
    top: 9px;
    z-index: 10;

    width: 35px;
    height: 6px;
    border-radius: 3px;

    background-color: @leaf;
  }

  &--left::before {
    right: -58px;
  }

  &--right::before {
    left: -61px;
  }
}

.example__radio--previous:checked ~ .example__toggles .example__toggle--left::before {
  display: block;
}

.example__radio--current:checked ~ .example__toggles .example__toggle--right::before {
  display: block;
}

.example__radio--previous:checked ~ .example__image-wrapper .example__image--before {
  display: block;
}

.example__radio--current:checked ~ .example__image-wrapper .example__image--after {
  display: block;
}

@media (min-width: @tablet-width) {
  .example {
    min-width: @tablet-width;
  }

  .example__wrapper {
    background-color: @white;
  }

  .example__content {
    min-width: @tablet-width;
    padding: 40px 30px 235px;
  }

  .example__title {
    margin-bottom: 64px;

    font-size: 60px;
    line-height: 60px;
  }

  .example__info {
    width: 676px;
    margin-bottom: 53px;

    font-size: 16px;
    line-height: 24px;
  }

  .example__statistic {
    width: 366px;
    margin-bottom: 0;
    margin-right: auto;
  }

  .example__costs {
    width: 174px;
    margin: 0;
    padding-top: 10px;

    .font-apply(@text-align: right);
    font-size: 20px;
    line-height: 25px;
  }

  .example__visual {
    position: relative;
    width: @tablet-width;
    min-height: 462px;
    padding: 369px 0 38px;

    background-color: @grey;
  }

  .example__image-wrapper {
    position: absolute;
    width: 384px;

    overflow-x: hidden;

    &--before {
      top: -175px;
    }

    &--after {
      left: 384px;
      top: -169px;
    }
  }

  .example__image {
    min-width: 690px;
    min-height: 517px;
    margin: 0;

    &--before {
      display: block;
      position: relative;
      left: 46px;
      z-index: 1;
    }

    &--after {
      display: block;
      position: relative;
      left: -335px;
      z-index: 1;
    }
  }

  .example__toggles {
    width: 568px;

    &::after {
      display: none;
    }
  }

  .example__toggle {
    padding-bottom: 6px;

    font-size: 20px;

    border-bottom: 1px dotted @underline-grey;

    &:hover,
    &:focus {
      border-bottom: 1px solid rgba(152, 152, 152, 0.4);
    }

    &:active {
      border-bottom: none;
    }

    &--left::before {
      display: none;
    }
  }

  .example__scale {
    position: relative;
    width: 428px;
    height: 6px;
    border-radius: 2.5px;

    background-color: @range-grey;

    border: none;
  }

  .example__range {
    position: absolute;
    top: -15px;
    left: 200px;
    z-index: 2;

    width: 34px;
    height: 34px;
    border-radius: 50%;

    background-color: @white;

    border: solid 1px @pinkish-grey;
    box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.15);

    &:hover,
    &:focus {
      box-shadow: 0 3px 5px 0 rgba(0, 0, 0, 0.25);
    }

    &:active {
      border: 2px solid @leaf;
      box-shadow: 0 4px 5px 0 rgba(0, 0, 0, 0.3);
    }
  }

  .example__point {
    position: absolute;
    top: 12px;
    left: 12px;
    z-index: 3;

    width: 8px;
    height: 8px;
    border-radius: 50%;

    background-color: @leaf;

    border: none;
  }

  .example__radio--previous:checked ~ .example__toggles .example__toggle--left::before {
    display: none;
  }

  .example__radio--current:checked ~ .example__toggles .example__toggle--right::before {
    display: none;
  }
}

@media (min-width: @desktop-width) {
  .example {
    min-width: @desktop-width;
  }

  .example__wrapper {
    width: auto;

    background-color: @grey;
  }

  .example__content {
    height: 462px;
    min-width: 580px;
    margin: 0;
    padding: 68px 0 67px 40px;
  }

  .example__title {
    width: 100%;
    margin-top: -205px;
    padding: 0;
  }

  .example__info {
    width: 431px;
    margin-bottom: 55px;
  }

  .example__statistic {
    margin-bottom: 42px;
  }

  .example__costs {
    width: 330px;
    margin: 0;
    padding-top: 0;

    .font-apply();
    font-size: 20px;
    line-height: 20px;
  }

  .example__costs span {
    margin-left: 57px;
  }

  .example__visual {
    width: 720px;
  }

  .example__image-wrapper {
    width: 395px;

    &--before {
      top: -183px;
    }

    &--after {
      left: 395px;
      top: -185px;
    }
  }

  .example__image {
    &--before {
      left: 30px;
    }

    &--after {
      left: -363px;
    }
  }
}
