.advantages {
  width: 320px;
  margin: 0 auto;
  padding: 26px 20px 45px;
}
.advantages__title {
  margin: 0;
  margin-bottom: 41px;
  font-weight: 400;
  font-style: normal;
  font-family: "Oswald", "Arial", sans-serif;
  text-align: left;
  font-size: 36px;
  line-height: 36px;
  color: #111111;
}
.advantages__list {
  list-style: none;
  margin: 0;
  padding: 0;
}
.advantages__item {
  position: relative;
  margin-bottom: 27px;
  padding-left: 80px;
  font-weight: 400;
  font-style: normal;
  font-family: "Arial", "Arial", sans-serif;
  text-align: left;
  font-size: 14px;
  line-height: 18px;
  color: #444444;
}
.advantages__item:last-of-type {
  margin-bottom: 0;
}
.advantages__item::before {
  content: "";
  position: absolute;
  top: -2px;
  left: 0;
  z-index: 2;
  width: 60px;
  height: 60px;
  background-color: #68b738;
  background-repeat: no-repeat;
  background-position: 5px 5px;
  background-size: 50px 50px;
}
.advantages__item--ingredients::before {
  background-image: url("../img/advantage-leaf@1x.png");
}
@media (min-resolution: 1.5dppx) {
  .advantages__item--ingredients::before {
    background-image: url("../img/advantage-leaf@2x.png");
  }
}
.advantages__item--comfort::before {
  background-image: url("../img/advantage-powder@1x.png");
}
@media (min-resolution: 1.5dppx) {
  .advantages__item--comfort::before {
    background-image: url("../img/advantage-powder@2x.png");
  }
}
.advantages__item--replacement::before {
  background-image: url("../img/advantage-dining@1x.png");
}
@media (min-resolution: 1.5dppx) {
  .advantages__item--replacement::before {
    background-image: url("../img/advantage-dining@2x.png");
  }
}
.advantages__item--time::before {
  background-image: url("../img/advantage-clock@1x.png");
}
@media (min-resolution: 1.5dppx) {
  .advantages__item--time::before {
    background-image: url("../img/advantage-clock@2x.png");
  }
}
@media (min-width: 768px) {
  .advantages {
    width: 768px;
    margin: 0 auto;
    padding: 43px 30px 105px;
    border-bottom: 1px solid #e6e6e6;
  }
  .advantages__title {
    margin-bottom: 63px;
    font-size: 60px;
    line-height: 60px;
  }
  .advantages__list {
    display: flex;
    flex-wrap: wrap;
    counter-reset: section;
  }
  .advantages__item {
    width: 246px;
    margin-right: 139px;
    margin-bottom: 125px;
    padding-top: 120px;
    padding-left: 0;
    font-size: 16px;
    line-height: 24px;
  }
  .advantages__item:nth-child(2n) {
    margin-right: auto;
  }
  .advantages__item:nth-last-of-type(2) {
    margin-bottom: 0;
  }
  .advantages__item::before {
    top: 8px;
    width: 80px;
    height: 80px;
    background-position: 15px 15px;
  }
  .advantages__item::after {
    position: absolute;
    top: -31px;
    right: 0;
    z-index: -1;
    font-weight: 400;
    font-style: normal;
    font-family: "Oswald", "Arial", sans-serif;
    text-align: left;
    font-size: 281px;
    line-height: 281px;
    color: #f2f2f2;
    counter-increment: section;
    content: "" counter(section) "";
  }
}
@media (min-width: 1300px) {
  .advantages {
    width: 1300px;
    padding: 75px 40px 185px;
    border-bottom: none;
  }
  .advantages__item {
    margin-right: auto;
    margin-bottom: 75px;
  }
  .advantages__item:nth-child(4n) {
    margin-right: 0;
  }
}
